.themeSwitch{
    width: 50px;
    margin: 5px;
    &__block-switch{
        display: flex;
        position: relative;
        input{
           opacity: 1;
            width: 0;
            height: 0;
        }
        &_circle{
            position: absolute;
            height: 20px;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: #E6D4FA;
            border-radius: 3rem;
            transition: 0.3s;
            cursor: pointer;
        }
        &_circle::before{
            position: absolute;
            content: '';
            width: 1.25rem;
            height: 1.25rem;
            top: 0.06rem;
            left: 0.12rem;
            bottom: 0;
            background-color: white;
            border-radius: 1.5rem;
            box-shadow: 0 0.15rem 0.3rem rgba(0, 0, 0, 0.5);
            transition: 0.3s;
            z-index: 2;
        }

        input:checked + &_circle{
            background-color: #9790F9;
        }

        input:checked + &_circle::before{
            transform: translateX(2rem);
        }

        &_sun{
            position: absolute;
            top: 0.1rem;
            right: 0.25rem;
            z-index: 1;
        }

        &_moon{
            position: absolute;
            top: 0.12rem;
            left: 0.22rem;
            z-index: 1;
        }

    }
    &__container{}
    &__contant{}
    &__block{}
    &__switch{
        display: flex;
        background-color: #E6D4FA;
        height: 20px;
        border-radius: 18px;
        cursor: pointer;
        position: relative;
        &-PM{
            background-color: #6C62F8;
            height: 20px;
            border-radius: 18px;
            cursor: pointer;
            position: relative;
        }
        &_circle{
            position: absolute;
            //display: flex;
            //align-items: center;
            margin-left: 30px;
            margin-top: 1px;
        }

        &_moon{
            display: flex;
            align-items: center;
            padding-left: 3px;
        }
        &_sun{
            display: flex;
            align-items: center;
            padding-right: 4px;
        }
    }
}
