.basic-info{
    flex-grow: 0.1;
    &__content{
        background-color: #E6E6FA;
        min-width: 250px;
        height: calc(100vh - 32px);
        padding-top: 30px;
    }
    &__photo{
        display: flex;
        justify-content: center;
        img {
            object-fit: cover;
            width: 200px;
            height: 280px;
            border-radius: 16px;
        }
    }
}